"use client";

import { useSearchParams } from "next/navigation";
import { useEffect, useState } from "react";
import { createSupabaseServer } from "@/lib/supabaseServer"; // server-side helper

export default async function WeekPage({ params }: { params: { id: string; week: string }}) {
  const { id, week } = params; // automatically 1, 2, etc.

  const [replays, setReplays] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!id || !week) return;

    const fetchReplays = async () => {
      const supabase = createSupabaseServer(); // NOTE: only works in server context
      const { data } = await supabase
        .from("replays")
        .select("*")
        .eq("division", Number(id))
        .eq("week", Number(week));
      setReplays(data || []);
      setLoading(false);
    };

    fetchReplays();
  }, [id, week]);

  if (!id || !week) {
    return <div style={{ padding: 40 }}><h1>Invalid Division or Week</h1></div>;
  }

  if (loading) {
    return <div style={{ padding: 40 }}>Loading...</div>;
  }

  return (
    <div style={{ padding: 40 }}>
      <h1>Division {id} â€” Week {week}</h1>

      {!replays.length && <p>No replays uploaded yet.</p>}

      {replays.map(r => (
        <div key={r.id} style={{ marginBottom: 20 }}>
          <strong>{r.team1} vs {r.team2}</strong>
          <br />
          <a href={r.file_url} target="_blank">View Replay</a>
        </div>
      ))}
    </div>
  );
}